/*key*/

.keyboard {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 5;
}

.keyboard table {
  width: 100%;
  border: 0;
}

.keyboard table tr td {
  width: 25%;
  text-align: center;
  height: 60px;
  line-height: 60px;
  font-size: 1.2em;
}

.keyboard table tr td a {
  border-radius: 3px;
  -webkit-border-radius: 3px;
  display: block;
  border: solid 1px #e7e7e7;
  background: -moz-linear-gradient(top, #fcfcfc, #efefef);
  filter: progid:DXImageTransform.Microsoft.gradient(
      startColorstr="#fcfcfc",
      endColorstr="#efefef"
    );
}

.keyboard table tr td a:hover {
  background: #dedede;
}

.keyboard table tr td.dd {
  border: solid 1px #6bcbca;
  color: #fff;
  vertical-align: middle;
  background: #6bcbca;
  background: -moz-linear-gradient(top, #6bcbca, #28a2a1);
  filter: progid:DXImageTransform.Microsoft.gradient(
      startColorstr="#6bcbca",
      endColorstr="#28a2a1"
    );
}

.keyboard table tr td i.bac {
  display: inline-block;
  width: 60%;
}

.keyboard table tr td i.bac img {
  width: 100%;
}

@media screen and (min-width: 100px) and (max-width: 320px) {
  .keyboard table tr td {
    width: 25%;
    text-align: center;
    height: 50px;
    line-height: 50px;
    font-size: 1em;
  }
}

